export default {
  props: {
    helpTextIcon: { default: "cyicon-help", type: String },
    helpTextClass: { default: "", type: String },
    helpTextStyle: { default: () => {}, type: Object },
    helpTextSize: { default: () => "sm", type: String },
    helpText: { default: () => null, type: [String, Function] },
    placement: { default: () => null, type: String }
  },
  computed: {
    getHelpText() {
      if (typeof helpText === "function") return this.helpText();
      return this.helpText;
    }
  }
};
